.texto{
  color: burlywood;
  font-size: 4em;
}

.conteiner{
  justify-content: center;
  text-align: center;
}

#classe{
  margin-bottom: 150px;
}
table {
  max-width: 500px;
  margin: auto;
  width: 500px;
  border-collapse: collapse;
  position: relative;
  cursor: context-menu;
  border: 1px solid black;
}

td, th {
  border: 1px solid black;
  padding: 10px;
}

thead {
  background-color: #383838;
  color: white;
}

thead > tr > th {
  position: sticky;
  top: -1px;
  background-color: #383838;
}
tbody > tr:nth-child(odd) { /* 2n-1 */
  background-color: white;
}
tbody > tr:nth-child(even) { /* 2n */
  background-color: lightgray;
}
td.num {
  text-align: right;
}
.btn{
  font-size: 30px;
  height: 35px;
  width: 55px;
  padding: 20px;
  background-color: black;
  color: white;
  border-color: black;
  text-align: center;
  outline: none;
}